<template>
  <div class="scrollBox" @touchmove.prevent="" style="z-index: 20">
    <header-nav @broadCast="backFn" v-if="$utils.showNavBar()" msg=""></header-nav>
    <div class="bind_phone">
      <div class="iconSize banner">
        <img src="../../../assets/img/bind_phone/bind.png" alt="">
        <div class="setRewardText">
          <br>{{100 * commonState.inviteVcoin}}
        </div>
      </div>
      <div class="bindWechatButton" @click="bindWechat">
        <div class="iconSize">
          <img src="../../../assets/img/personal/login_wechat.png" alt="">
        </div>
        <div class="bind-text"></div>
      </div>
    </div>
    <prompt-box
      :dropBol="showConfirm"
      @leftBtn="cancelFn"
      @rightBtn="confirmFn"
      leftTxt=""
      rightTxt=""
      contentTxt="<br><br>">
    </prompt-box>
  </div>
</template>
<script>
import HeaderNav from './bindTitle'
// import p
import {mapState, mapMutations} from 'vuex'
import PromptBox from '../../common/prompt-box'
export default {
  components: {
    HeaderNav,
    PromptBox
  },
  computed: mapState({
    commonState: state => state.common,
    share: state => state.share
  }),
  data () {
    this.origin_url = this.$config.currentApi + '/v1/passport/captcha?timestamp='
    return {
      showConfirm: false,
      timestamp: new Date().getTime(),
      couldSendMsg: true,
      placeholder: {
        phone: '',
        capture: '',
        code: ''
      },
      showCapture: false,
      buttonText: '',
      phoneData: {
        mobile: '',
        code: '',
        capture: ''
      }
    }
  },
  beforeUpdate () {
    this.setInput()
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'common_set_guild',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_columnid_change',
      'common_set_hasRed'
    ]),
    // 
    getUserInfo (cb) {
      this.$api.get('GET_USERINFO', {}, res => {
        this.common_set_userInfo(Object.assign(this.commonState.userInfo, res.data))
        typeof cb === 'function' && cb()
      })
    },
    setInput () {
      if (this.phoneData.mobile) {
        this.phoneData.mobile = this.phoneData.mobile.replace(/[^0-9]/g, '')
        if (this.phoneData.mobile.length > 11) {
          this.phoneData.mobile = this.phoneData.mobile.substr(0, 11)
        }
      }
      if (this.phoneData.code) {
        this.phoneData.code = this.phoneData.code.replace(/[^0-9]/g, '')
        if (this.phoneData.code.length > 11) {
          this.phoneData.code = this.phoneData.code.substr(0, 4)
        }
      }
    },
    // 
    backFn () {
      // this.$emit('closeBind', {from: 'bind_phone', action: ''})
      this.$emit('closeBind', {from: 'bind_phone', action: ''})
    },
    cancelFn () {
      this.showConfirm = false
      this.$emit('closeBind', {from: 'bind_wechat', action: ''})
    },
    confirmFn () {
      this.$emit('closeBind', {from: 'bind_wechat', action: '', mobile: this.phoneData.mobile})
    },
    bindWechat () {
      this.$utils.plusReady(plus => {
        if (this.commonState.oauthInfo['weixin']) {
          var w = plus.nativeUI.showWaiting('')
          setTimeout(() => {
            w.close()
          }, 5000)
          this.commonState.oauthInfo['weixin'].authorize(res => {
            let data = {
              code: res.code
            }
            this.$api.post('BIND_WECHAT_NEW', data, res => {
              w.close()
              if (res.data.user_token) {
                this.$utils.cache('user_token', res.data.user_token)
              }
              if (res.data.coin > 0) {
                let shareSuccessObj = Object.assign({type: 3, tipText: `${this.$utils.splitStr(res.data.wechat_nickname)}`}, res.data)
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj
                })
                this.set_shareType(16)
                this.setShowShare(true)
              } else {
                this.$emit('closeBind', {
                  from: 'bind_wechat',
                  action: ''
                })
                this.$toast({
                  message: `${this.$utils.splitStr(res.data.wechat_nickname)}`,
                  duration: 1500
                })
              }
              this.getUserInfo()
            }, res => {
              w.close()
              if (res.code === 666) {
                this.showConfirm = true
              }
            }, catchErr => {
              w.close()
            })
          }, error => {
            w.close()
            if (error.code === -2) {
              plus.nativeUI.toast('')
            } else {
              plus.nativeUI.toast(':' + JSON.stringify(error))
            }
          })
        } else {
          this.$toast('')
        }
      })
    }
  },
  watch: {
    'share.showSuccessShare' (flag) {
      if (!flag) {
        if (this.share.shareType === 16) {
          this.$emit('closeBind', {
            from: 'bind_wechat',
            action: ''
          })
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.scrollBox {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  .bind_phone {
    background: #f5f5f5;
    height: 100vh;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding-top: 30px;
    box-sizing: border-box;
    .banner {
      height: 206px;
      position: relative;
      .setRewardText {
        font-size: 46px;
        line-height: 65px;
        color: #fff;
        font-weight: 900;
        position: absolute;
        left: 36px;
        top: 40px;
      }
    }
    .bindWechatButton {
      display: flex;
      justify-content: center;
      align-items: center;
      background: #24B637;
      box-shadow: 0 2px 4px 0 #28BE3F;
      border-radius: 40.5px;
      width: 637px;
      height: 81px;
      color: #fff;
      margin: 100px auto;
      .iconSize {
        width: 57px;
        height: 43px;
      }
    }
  }
}
</style>



